<template>
  <div class="home">
    <v-container fluid grid-list-lg>
      <v-layout row wrap>
        <v-flex xs12 class="text-xs-center mb-0 pb-0">
          <canvas ref="logo" id="logo" width="200" height="190"></canvas>
        </v-flex>
        <v-flex xs12 class="text-xs-center mt-0 pt-0">
          <v-btn href="https://t.me/tbhtk" target="tbhtk@telegram" dark class="light-blue darken-1 no-uppercase">
            <v-icon left dark>mdi-telegram</v-icon>
            JOIN (@tbhtk)</v-btn>
        </v-flex>
      </v-layout>
      <v-layout row wrap class="mt-3">
        <v-flex v-for="(item, index) in items" :key="index" :class="['xs12 sm6 md4', {'lg3': items.length > 4} ]">
          <item-card :coverImage="item.image" :url="item.url" :desc="item.desc" :color="item.color" :cardClass="item.class"></item-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import axios from 'axios'
import ItemCard from '../components/ItemCard.vue'
import logoSpritesheet from '../spritesheets/logo'

export default {
  name: 'home',
  components: {
    ItemCard
  },
  // directives: {
  //   insertMessage: function (canvasElement, binding) { }
  // },
  created() {
  },
  mounted() {
    this.init()
    this.loadData()
  },
  methods: {
    init() {
      this.createLogo()
      this.$nextTick(function () {
        window.addEventListener('resize', (e) => {
          // const width = e.target.outerWidth
          // const height = e.target.outerHeight
        })
      })
    },
    createLogo() {
      const createjs = window.createjs
      const stage = new createjs.Stage(this.$refs.logo)
      this.logoStage = stage
      createjs.Ticker.setFPS(24)
      createjs.Ticker.addEventListener('tick', stage)

      const ss = new createjs.SpriteSheet(logoSpritesheet)
      const logo = new createjs.Sprite(ss)
      logo.x = 0
      logo.y = 0
      logo.scaleX = 0.5
      logo.scaleY = 0.5
      logo.on('click', function () {
        this.gotoAndPlay('run')
      })
      stage.addChild(logo)

      setTimeout(() => {
        logo.gotoAndPlay('run')
      }, 500)
    },

    async loadData() {
      try {
        let res = await axios.get('/static/data.json')
        this.items = res.data.articles
      } catch (error) {

      }
    }
  },
  data() {
    return {
      logoStage: null,
      exampleContent: 'This is TEXT',
      msg: '',
      items: [],
    }
  }
}
</script>


  <style src="vuetify/dist/vuetify.min.css"></style>
<style scoped>
.no-uppercase {
  text-transform: none !important
}

.full-height .flex {
  display: flex
}

.full-height .flex>.card {
  flex: 1 1 auto
}
</style>



